import { useEffect, useRef, useState } from 'react'
import { useOutletContext } from 'react-router-dom'
import * as d3 from 'd3'

function BudgetResources() {
  const { language, compareMode, lakeId } = useOutletContext()
  const currentSvgRef = useRef()
  const idealSvgRef = useRef()
  const [budgetData, setBudgetData] = useState([])

  useEffect(() => {
    if (!lakeId) return

    fetch(`/data/lakes/${lakeId}/budget-allocations.json`)
      .then(res => res.json())
      .then(data => setBudgetData(data))
      .catch(err => console.error('Error loading budget data:', err))
  }, [lakeId])

  useEffect(() => {
    if (budgetData.length === 0) return

    if (currentSvgRef.current) {
      createBudgetFlow(currentSvgRef.current, budgetData, 'current')
    }

    if (compareMode && idealSvgRef.current) {
      createBudgetFlow(idealSvgRef.current, budgetData, 'ideal')
    }
  }, [budgetData, compareMode])

  function createBudgetFlow(container, data, state) {
    d3.select(container).selectAll('*').remove()

    const width = container.clientWidth || 600
    const height = 500
    const margin = { top: 40, right: 120, bottom: 40, left: 180 }

    const svg = d3.select(container)
      .attr('width', width)
      .attr('height', height)

    // Group allocations by source
    const nationalBudget = data.filter(d => d.source === 'National Budget')
    const provincialBudget = data.filter(d => d.source === 'Provincial Budget')
    const farmerCompensation = data.find(d => d.target === 'Displaced Farmers')

    // Calculate positions
    const sourceX = margin.left
    const targetX = width - margin.right
    const midX = (sourceX + targetX) / 2

    // Color scale based on impact
    const impactColors = {
      'highly_negative': '#ff6b6b',  // Urgency Red
      'negative': '#ff8787',
      'neutral': '#94a3b8',
      'positive': '#51cf66',  // Hope Green
    }

    // Prepare flow data for current state
    let flows = []

    if (state === 'current') {
      // Source nodes
      const sources = [
        { id: 'national', label: 'National Budget', y: 100, total: d3.sum(nationalBudget, d => d.amount) },
        { id: 'provincial', label: 'Provincial Budget', y: 200, total: d3.sum(provincialBudget, d => d.amount) }
      ]

      // Target nodes - highlight the asymmetry
      const militaryAllocation = data.find(d => d.target === 'Kodam IV Diponegoro')
      const farmerAllocation = farmerCompensation

      flows = [
        // Military: 156 billion
        {
          source: 'national',
          target: 'military',
          amount: militaryAllocation.amount,
          label: 'Kodam IV\n(Hyacinth Removal)',
          targetY: 80,
          color: impactColors.negative
        },
        // BWS
        {
          source: 'national',
          target: 'bws',
          amount: 50000000000,
          label: 'BWS\n(Dredging)',
          targetY: 150,
          color: impactColors.neutral
        },
        // Provincial monitoring
        {
          source: 'provincial',
          target: 'dlh',
          amount: 15000000000,
          label: 'DLH\n(Monitoring)',
          targetY: 220,
          color: impactColors.neutral
        },
        // Farmers: ZERO
        {
          source: 'any',
          target: 'farmers',
          amount: 0,
          label: 'Displaced Farmers\n(ZERO)',
          targetY: 350,
          color: impactColors.highly_negative,
          broken: true
        }
      ]
    } else {
      // Ideal state: balanced allocation
      flows = [
        {
          source: 'integrated',
          target: 'prevention',
          amount: 80000000000,
          label: 'Pollution Prevention\n(Root Cause)',
          targetY: 80,
          color: impactColors.positive
        },
        {
          source: 'integrated',
          target: 'infrastructure',
          amount: 60000000000,
          label: 'Infrastructure\n(Sustainable)',
          targetY: 150,
          color: impactColors.positive
        },
        {
          source: 'integrated',
          target: 'compensation',
          amount: 40000000000,
          label: 'Farmer Compensation\n(Livelihood Support)',
          targetY: 220,
          color: impactColors.positive
        },
        {
          source: 'integrated',
          target: 'monitoring',
          amount: 20000000000,
          label: 'Monitoring & Data\n(Evidence-based)',
          targetY: 290,
          color: impactColors.positive
        }
      ]
    }

    // Draw flows
    if (state === 'current') {
      // Draw source nodes (left side)
      const sourceGroup = svg.append('g')

      sourceGroup.append('rect')
        .attr('x', sourceX - 160)
        .attr('y', 80)
        .attr('width', 150)
        .attr('height', 50)
        .attr('fill', '#007acc')
        .attr('rx', 5)

      sourceGroup.append('text')
        .attr('x', sourceX - 85)
        .attr('y', 105)
        .attr('text-anchor', 'middle')
        .attr('fill', 'white')
        .attr('font-size', '12px')
        .attr('font-weight', 'bold')
        .text('National Budget')

      sourceGroup.append('text')
        .attr('x', sourceX - 85)
        .attr('y', 122)
        .attr('text-anchor', 'middle')
        .attr('fill', 'white')
        .attr('font-size', '10px')
        .text('246+ Billion IDR')

      sourceGroup.append('rect')
        .attr('x', sourceX - 160)
        .attr('y', 180)
        .attr('width', 150)
        .attr('height', 50)
        .attr('fill', '#2989d8')
        .attr('rx', 5)

      sourceGroup.append('text')
        .attr('x', sourceX - 85)
        .attr('y', 205)
        .attr('text-anchor', 'middle')
        .attr('fill', 'white')
        .attr('font-size', '12px')
        .attr('font-weight', 'bold')
        .text('Provincial Budget')

      sourceGroup.append('text')
        .attr('x', sourceX - 85)
        .attr('y', 222)
        .attr('text-anchor', 'middle')
        .attr('fill', 'white')
        .attr('font-size', '10px')
        .text('20+ Billion IDR')

      // Draw flows (curved paths)
      flows.forEach((flow, i) => {
        const startY = flow.source === 'national' ? 105 : (flow.source === 'provincial' ? 205 : 300)
        const endY = flow.targetY + 25

        if (flow.broken) {
          // Draw broken line for zero allocation
          const path = d3.path()
          path.moveTo(sourceX + 20, startY)
          path.lineTo(targetX - 20, endY)

          svg.append('path')
            .attr('d', path.toString())
            .attr('stroke', flow.color)
            .attr('stroke-width', 2)
            .attr('stroke-dasharray', '5,5')
            .attr('fill', 'none')
            .attr('opacity', 0.5)

          // X mark
          svg.append('text')
            .attr('x', midX)
            .attr('y', endY)
            .attr('text-anchor', 'middle')
            .attr('fill', '#ff6b6b')
            .attr('font-size', '30px')
            .attr('font-weight', 'bold')
            .text('✗')
        } else {
          // Draw curved flow
          const path = d3.path()
          path.moveTo(sourceX + 20, startY)
          path.bezierCurveTo(
            midX, startY,
            midX, endY,
            targetX - 20, endY
          )

          svg.append('path')
            .attr('d', path.toString())
            .attr('stroke', flow.color)
            .attr('stroke-width', Math.max(3, (flow.amount / 156000000000) * 30))
            .attr('fill', 'none')
            .attr('opacity', 0.6)

          // Amount label on flow
          svg.append('text')
            .attr('x', midX)
            .attr('y', (startY + endY) / 2 - 5)
            .attr('text-anchor', 'middle')
            .attr('fill', '#333')
            .attr('font-size', '10px')
            .attr('font-weight', 'bold')
            .text(`${(flow.amount / 1000000000).toFixed(0)}B IDR`)
        }

        // Target label
        const lines = flow.label.split('\n')
        const textGroup = svg.append('g')

        textGroup.append('rect')
          .attr('x', targetX - 10)
          .attr('y', flow.targetY)
          .attr('width', 110)
          .attr('height', 50)
          .attr('fill', flow.broken ? '#fee' : '#f8f9fa')
          .attr('stroke', flow.color)
          .attr('stroke-width', 2)
          .attr('rx', 5)

        lines.forEach((line, j) => {
          textGroup.append('text')
            .attr('x', targetX + 45)
            .attr('y', flow.targetY + 20 + (j * 14))
            .attr('text-anchor', 'middle')
            .attr('fill', '#333')
            .attr('font-size', '11px')
            .attr('font-weight', line.includes('ZERO') ? 'bold' : 'normal')
            .text(line)
        })
      })

      // Title
      svg.append('text')
        .attr('x', width / 2)
        .attr('y', 25)
        .attr('text-anchor', 'middle')
        .attr('font-size', '16px')
        .attr('font-weight', 'bold')
        .attr('fill', '#ff6b6b')
        .text('Current Budget Asymmetry')

    } else {
      // Ideal state: integrated budget
      svg.append('rect')
        .attr('x', sourceX - 160)
        .attr('y', 120)
        .attr('width', 150)
        .attr('height', 60)
        .attr('fill', '#51cf66')
        .attr('rx', 5)

      svg.append('text')
        .attr('x', sourceX - 85)
        .attr('y', 145)
        .attr('text-anchor', 'middle')
        .attr('fill', 'white')
        .attr('font-size', '12px')
        .attr('font-weight', 'bold')
        .text('Integrated')

      svg.append('text')
        .attr('x', sourceX - 85)
        .attr('y', 160)
        .attr('text-anchor', 'middle')
        .attr('fill', 'white')
        .attr('font-size', '12px')
        .attr('font-weight', 'bold')
        .text('Budget Planning')

      svg.append('text')
        .attr('x', sourceX - 85)
        .attr('y', 172)
        .attr('text-anchor', 'middle')
        .attr('fill', 'white')
        .attr('font-size', '10px')
        .text('200 Billion IDR')

      // Draw balanced flows
      flows.forEach((flow) => {
        const startY = 150
        const endY = flow.targetY + 25

        const path = d3.path()
        path.moveTo(sourceX + 20, startY)
        path.bezierCurveTo(
          midX, startY,
          midX, endY,
          targetX - 20, endY
        )

        svg.append('path')
          .attr('d', path.toString())
          .attr('stroke', flow.color)
          .attr('stroke-width', Math.max(5, (flow.amount / 80000000000) * 20))
          .attr('fill', 'none')
          .attr('opacity', 0.7)

        // Amount
        svg.append('text')
          .attr('x', midX)
          .attr('y', (startY + endY) / 2 - 5)
          .attr('text-anchor', 'middle')
          .attr('fill', '#333')
          .attr('font-size', '10px')
          .attr('font-weight', 'bold')
          .text(`${(flow.amount / 1000000000).toFixed(0)}B IDR`)

        // Target label
        const lines = flow.label.split('\n')
        const textGroup = svg.append('g')

        textGroup.append('rect')
          .attr('x', targetX - 10)
          .attr('y', flow.targetY)
          .attr('width', 120)
          .attr('height', 50)
          .attr('fill', '#e8f5e9')
          .attr('stroke', flow.color)
          .attr('stroke-width', 2)
          .attr('rx', 5)

        lines.forEach((line, j) => {
          textGroup.append('text')
            .attr('x', targetX + 50)
            .attr('y', flow.targetY + 18 + (j * 13))
            .attr('text-anchor', 'middle')
            .attr('fill', '#333')
            .attr('font-size', '10px')
            .text(line)
        })
      })

      // Title
      svg.append('text')
        .attr('x', width / 2)
        .attr('y', 25)
        .attr('text-anchor', 'middle')
        .attr('font-size', '16px')
        .attr('font-weight', 'bold')
        .attr('fill', '#51cf66')
        .text('Balanced Resource Allocation')
    }
  }

  const content = {
    en: {
      title: 'Budget & Resource Flows',
      subtitle: 'Resource Allocation & Power Asymmetry Through Funding',
      currentLabel: 'Current Budget Asymmetry',
      idealLabel: 'Balanced Resource Allocation',
      description: 'This flow visualization exposes power asymmetry through budget allocation. The current state shows 156 billion rupiah allocated to military contractors for symptom treatment (water hyacinth removal), while displaced farmers received ZERO compensation for 2.5 years of livelihood loss. The ideal state demonstrates balanced allocation addressing root causes with community support.',
      asymmetryTitle: '156 Billion Rupiah Asymmetry',
      asymmetryDesc: 'Military contractors received 156 billion for hyacinth removal (treating symptoms), while farmers banned from farming for 2.5 years (2020-2022) received zero compensation. This budget allocation reveals who holds power and whose livelihoods matter in governance decisions.',
      keyFinding: 'Key Finding: Budget Reveals True Priorities',
      keyFindingDesc: 'Resource flows show governance priorities more accurately than policy documents. The 156B military contract vs. zero farmer compensation exposes whose interests are protected.'
    },
    id: {
      title: 'Anggaran & Aliran Sumber Daya',
      subtitle: 'Alokasi Sumber Daya & Asimetri Kekuasaan Melalui Pendanaan',
      currentLabel: 'Asimetri Anggaran Saat Ini',
      idealLabel: 'Alokasi Sumber Daya Seimbang',
      description: 'Visualisasi aliran ini mengungkap asimetri kekuasaan melalui alokasi anggaran. Kondisi saat ini menunjukkan 156 miliar rupiah dialokasikan ke kontraktor militer untuk pengobatan gejala (pembersihan enceng gondok), sementara petani yang dipindahkan menerima NOL kompensasi untuk 2,5 tahun kehilangan mata pencaharian. Kondisi ideal mendemonstrasikan alokasi seimbang yang mengatasi akar masalah dengan dukungan masyarakat.',
      asymmetryTitle: 'Asimetri 156 Miliar Rupiah',
      asymmetryDesc: 'Kontraktor militer menerima 156 miliar untuk pembersihan enceng gondok (mengobati gejala), sementara petani yang dilarang bertani selama 2,5 tahun (2020-2022) menerima nol kompensasi. Alokasi anggaran ini mengungkap siapa yang memegang kekuasaan dan mata pencaharian siapa yang penting dalam keputusan tata kelola.',
      keyFinding: 'Temuan Kunci: Anggaran Mengungkap Prioritas Sebenarnya',
      keyFindingDesc: 'Aliran sumber daya menunjukkan prioritas tata kelola lebih akurat daripada dokumen kebijakan. Kontrak militer 156M vs. kompensasi petani nol mengungkap kepentingan siapa yang dilindungi.'
    }
  }

  const t = content[language]

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-3xl font-bold text-lake-dark">{t.title}</h1>
        <p className="text-gray-600 mt-2">{t.subtitle}</p>
      </div>

      {/* Description */}
      <div className="module-card">
        <p className="text-gray-700">{t.description}</p>
      </div>

      {/* Visualizations */}
      <div className={`grid ${compareMode ? 'grid-cols-2' : 'grid-cols-1'} gap-6`}>
        {/* Current State */}
        <div className="module-card current-state">
          <h3 className="text-lg font-semibold text-urgency-red mb-4">{t.currentLabel}</h3>
          <svg ref={currentSvgRef} className="w-full border border-gray-200 rounded"></svg>

          <div className="mt-4 p-3 bg-red-50 border-l-4 border-urgency-red">
            <h4 className="font-semibold text-sm">{t.asymmetryTitle}</h4>
            <p className="text-sm text-gray-700 mt-1">{t.asymmetryDesc}</p>
          </div>

          <div className="mt-3 grid grid-cols-2 gap-3 text-sm">
            <div className="p-3 bg-red-100 rounded">
              <div className="font-bold text-2xl text-urgency-red">156B</div>
              <div className="text-gray-700">Military Contract</div>
              <div className="text-xs text-gray-600">Symptom treatment</div>
            </div>
            <div className="p-3 bg-gray-100 rounded">
              <div className="font-bold text-2xl text-gray-600">0</div>
              <div className="text-gray-700">Farmer Compensation</div>
              <div className="text-xs text-gray-600">2.5-year ban</div>
            </div>
          </div>
        </div>

        {/* Ideal State */}
        {compareMode && (
          <div className="module-card ideal-state">
            <h3 className="text-lg font-semibold text-hope-green mb-4">{t.idealLabel}</h3>
            <svg ref={idealSvgRef} className="w-full border border-gray-200 rounded"></svg>

            <div className="mt-4 p-3 bg-green-50 border-l-4 border-hope-green">
              <h4 className="font-semibold text-sm">Balanced Allocation</h4>
              <p className="text-sm text-gray-700 mt-1">
                Integrated budget planning addresses root causes (pollution prevention) while providing livelihood support (farmer compensation). Resources allocated based on evidence, not power.
              </p>
            </div>

            <div className="mt-3 grid grid-cols-2 gap-3 text-sm">
              <div className="p-3 bg-green-100 rounded">
                <div className="font-bold text-lg text-hope-green">80B</div>
                <div className="text-gray-700 text-xs">Pollution Prevention</div>
              </div>
              <div className="p-3 bg-green-100 rounded">
                <div className="font-bold text-lg text-hope-green">40B</div>
                <div className="text-gray-700 text-xs">Farmer Compensation</div>
              </div>
            </div>
          </div>
        )}
      </div>

      {/* Key Finding */}
      <div className="module-card bg-blue-50">
        <h3 className="text-lg font-semibold mb-2">{t.keyFinding}</h3>
        <p className="text-gray-700">{t.keyFindingDesc}</p>
      </div>
    </div>
  )
}

export default BudgetResources
