import { Outlet, Link, useLocation, useParams, useNavigate } from 'react-router-dom'
import { useState, useEffect } from 'react'

const modules = [
  { path: 'governance', name: 'Governance Network', icon: '🕸️' },
  { path: 'environmental', name: 'Environmental Monitoring', icon: '💧' },
  { path: 'land', name: 'Land & Spatial', icon: '🗺️' },
  { path: 'policy', name: 'Policy Framework', icon: '📜' },
  { path: 'budget', name: 'Budget & Resources', icon: '💰' },
  { path: 'community', name: 'Community & Justice', icon: '👥' },
  { path: 'information', name: 'Information Systems', icon: '💾' },
]

function Dashboard() {
  const { lakeId } = useParams()
  const location = useLocation()
  const navigate = useNavigate()
  const [language, setLanguage] = useState('en') // 'en' or 'id'
  const [compareMode, setCompareMode] = useState(true)
  const [lakeMetadata, setLakeMetadata] = useState(null)
  const [allLakes, setAllLakes] = useState([])

  // Load lake metadata
  useEffect(() => {
    if (!lakeId) return

    Promise.all([
      fetch(`/data/lakes/${lakeId}/meta.json`).then(res => res.json()),
      fetch('/data/lakes/index.json').then(res => res.json())
    ])
      .then(([meta, index]) => {
        setLakeMetadata(meta)
        setAllLakes(index.lakes)
      })
      .catch(err => console.error('Error loading lake metadata:', err))
  }, [lakeId])

  const handleLakeChange = (newLakeId) => {
    // Navigate to the same module path but for different lake
    const currentModule = location.pathname.split('/').pop()
    if (currentModule && modules.find(m => m.path === currentModule)) {
      navigate(`/${newLakeId}/${currentModule}`)
    } else {
      navigate(`/${newLakeId}`)
    }
  }

  return (
    <div className="flex min-h-screen">
      {/* Sidebar Navigation */}
      <aside className="w-64 bg-lake-dark text-white p-6 flex flex-col">
        <div className="mb-8">
          <h1 className="text-2xl font-bold text-water-blue">Lake Information System</h1>
          <p className="text-sm text-gray-400 mt-2">
            {lakeMetadata ? lakeMetadata.name[language] : 'Loading...'}
          </p>
        </div>

        {/* Lake Switcher */}
        {allLakes.length > 0 && (
          <div className="mb-6">
            <label className="text-sm text-gray-400 block mb-2">Select Lake</label>
            <select
              value={lakeId}
              onChange={(e) => handleLakeChange(e.target.value)}
              className="w-full px-3 py-2 bg-gray-800 rounded text-white hover:bg-gray-700 transition-colors text-sm"
            >
              {allLakes.map(lake => (
                <option key={lake.id} value={lake.id}>
                  {lake.name[language]} ({lake.region})
                </option>
              ))}
            </select>
          </div>
        )}

        {/* Module Navigation */}
        <nav className="flex-1 space-y-2">
          {modules.map((module) => {
            const currentPath = location.pathname.split('/').pop()
            const isActive = currentPath === module.path ||
                            (location.pathname === `/${lakeId}` && module.path === 'governance')
            return (
              <Link
                key={module.path}
                to={`/${lakeId}/${module.path}`}
                className={`block px-4 py-3 rounded transition-colors ${
                  isActive
                    ? 'bg-trust-blue text-white'
                    : 'text-gray-300 hover:bg-gray-800'
                }`}
              >
                <span className="mr-2">{module.icon}</span>
                {module.name}
              </Link>
            )
          })}
        </nav>

        {/* Controls */}
        <div className="mt-8 space-y-4 pt-4 border-t border-gray-700">
          {/* Language Toggle */}
          <div>
            <label className="text-sm text-gray-400 block mb-2">Language</label>
            <button
              onClick={() => setLanguage(language === 'en' ? 'id' : 'en')}
              className="w-full px-3 py-2 bg-gray-800 rounded hover:bg-gray-700 transition-colors text-sm"
            >
              {language === 'en' ? '🇬🇧 English' : '🇮🇩 Bahasa Indonesia'}
            </button>
          </div>

          {/* Compare Mode Toggle */}
          <div>
            <label className="text-sm text-gray-400 block mb-2">View Mode</label>
            <button
              onClick={() => setCompareMode(!compareMode)}
              className="w-full px-3 py-2 bg-gray-800 rounded hover:bg-gray-700 transition-colors text-sm"
            >
              {compareMode ? '⚖️ Compare Mode' : '📊 Single View'}
            </button>
          </div>
        </div>

        {/* Footer */}
        <div className="mt-6 pt-4 border-t border-gray-700 text-xs text-gray-500">
          <p>CSIRO Research Project</p>
          <p className="mt-1">
            {lakeMetadata ? `${lakeMetadata.name[language]} Analysis 2025` : 'Lake Governance Analysis'}
          </p>
          <Link to="/" className="text-water-blue hover:underline mt-2 block">
            ← All Lakes
          </Link>
        </div>
      </aside>

      {/* Main Content */}
      <main className="flex-1 p-8">
        <Outlet context={{ language, compareMode, lakeId, lakeMetadata }} />
      </main>
    </div>
  )
}

export default Dashboard
