import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom'
import Dashboard from './components/layout/Dashboard'
import LandingPage from './pages/LandingPage'
import GovernanceNetwork from './components/modules/GovernanceNetwork'
import EnvironmentalMonitoring from './components/modules/EnvironmentalMonitoring'
import LandSpatial from './components/modules/LandSpatial'
import PolicyFramework from './components/modules/PolicyFramework'
import BudgetResources from './components/modules/BudgetResources'
import CommunityJustice from './components/modules/CommunityJustice'
import InfoSystemsInfra from './components/modules/InfoSystemsInfra'

function App() {
  return (
    <Router>
      <div className="min-h-screen bg-lake-light">
        <Routes>
          {/* Landing page showing all lakes */}
          <Route path="/" element={<LandingPage />} />

          {/* Individual lake routes with /:lakeId parameter */}
          <Route path="/:lakeId" element={<Dashboard />}>
            <Route index element={<GovernanceNetwork />} />
            <Route path="governance" element={<GovernanceNetwork />} />
            <Route path="environmental" element={<EnvironmentalMonitoring />} />
            <Route path="land" element={<LandSpatial />} />
            <Route path="policy" element={<PolicyFramework />} />
            <Route path="budget" element={<BudgetResources />} />
            <Route path="community" element={<CommunityJustice />} />
            <Route path="information" element={<InfoSystemsInfra />} />
          </Route>

          {/* Comparison routes (future) */}
          {/* <Route path="/compare" element={<CrossLakeComparison />} /> */}

          {/* Catch-all redirect to landing page */}
          <Route path="*" element={<Navigate to="/" replace />} />
        </Routes>
      </div>
    </Router>
  )
}

export default App
