import { useEffect, useRef, useState } from 'react'
import { useOutletContext } from 'react-router-dom'
import * as d3 from 'd3'

function EnvironmentalMonitoring() {
  const { language, compareMode, lakeId } = useOutletContext()
  const currentMapRef = useRef(null)
  const idealMapRef = useRef(null)
  const trendRef = useRef(null)
  const hyacinthRef = useRef(null)
  const [stations, setStations] = useState([])
  const [trends, setTrends] = useState([])

  // Bilingual content
  const content = {
    en: {
      title: 'Environmental Monitoring Hub',
      subtitle: 'Data Collection Without Action',
      description: 'DLH Province operates 29 monitoring stations (5 lake primary, 24 tributary secondary) measuring biannual water quality. All lake stations consistently show hypereutrophic status (TSI 70-78), yet no enforcement action addresses root causes: 21× overcapacity KJA floating cages and unchecked agricultural runoff.',
      currentTitle: 'Current: Monitoring Without Response',
      idealTitle: 'Ideal: Real-Time Alert & Enforcement System',
      currentDescription: '29 stations collect data biannually. Reports document hypereutrophic crisis. No rapid response mechanism. No penalty enforcement for KJA overcapacity violations.',
      idealDescription: 'Real-time sensors with automated alerts. Integrated with enforcement authority. Rapid response teams deployed when thresholds exceeded. KJA capacity actively enforced.',
      trendTitle: 'Water Quality Degradation (2020-2024)',
      trendDescription: 'Trophic State Index (TSI) shows worsening hypereutrophic conditions despite monitoring infrastructure.',
      hyacinthTitle: 'Water Hyacinth Coverage Expansion (2020-2024)',
      hyacinthDescription: 'Enceng gondok coverage grew from 180 to 280 hectares. Symptom treatment (156B military removal) fails to address nutrient pollution root cause.',
      keyFinding: 'Monitoring Infrastructure Paradox',
      keyFindingText: '29 stations. Biannual measurements. 5 years of hypereutrophic data. Yet KJA floating cages remain 21× over scientific carrying capacity. Monitoring without enforcement is performative governance.',
      stationTypes: {
        primary: 'Primary Lake Station',
        secondary: 'Tributary Station'
      },
      statusLabels: {
        hypereutrophic: 'Hypereutrophic (Critical)',
        eutrophic: 'Eutrophic (High)',
        mesotrophic: 'Mesotrophic (Moderate)'
      },
      metrics: {
        tsi: 'Trophic State Index',
        phosphate: 'Phosphate (mg/L)',
        hyacinth: 'Hyacinth Coverage (ha)',
        stations: 'Monitoring Stations'
      },
      actions: {
        current: 'Data collection only',
        ideal: 'Real-time enforcement'
      }
    },
    id: {
      title: 'Hub Monitoring Lingkungan',
      subtitle: 'Pengumpulan Data Tanpa Aksi',
      description: 'DLH Provinsi mengoperasikan 29 stasiun monitoring (5 danau primer, 24 anak sungai sekunder) mengukur kualitas air dua kali setahun. Semua stasiun danau secara konsisten menunjukkan status hypereutrophic (TSI 70-78), namun tidak ada tindakan penegakan yang mengatasi akar masalah: KJA keramba jaring apung 21× melebihi kapasitas dan limpasan pertanian yang tidak terkendali.',
      currentTitle: 'Saat Ini: Monitoring Tanpa Respons',
      idealTitle: 'Ideal: Sistem Alert & Penegakan Real-Time',
      currentDescription: '29 stasiun mengumpulkan data dua kali setahun. Laporan mendokumentasikan krisis hypereutrophic. Tidak ada mekanisme respons cepat. Tidak ada penegakan hukuman untuk pelanggaran kapasitas KJA.',
      idealDescription: 'Sensor real-time dengan peringatan otomatis. Terintegrasi dengan otoritas penegakan. Tim respons cepat dikerahkan saat ambang batas terlampaui. Kapasitas KJA ditegakkan secara aktif.',
      trendTitle: 'Degradasi Kualitas Air (2020-2024)',
      trendDescription: 'Indeks Status Trofik (TSI) menunjukkan kondisi hypereutrophic yang memburuk meskipun ada infrastruktur monitoring.',
      hyacinthTitle: 'Ekspansi Eceng Gondok (2020-2024)',
      hyacinthDescription: 'Cakupan enceng gondok tumbuh dari 180 ke 280 hektar. Pengobatan gejala (156 miliar pembersihan militer) gagal mengatasi akar penyebab polusi nutrisi.',
      keyFinding: 'Paradoks Infrastruktur Monitoring',
      keyFindingText: '29 stasiun. Pengukuran dua kali setahun. 5 tahun data hypereutrophic. Namun KJA keramba jaring apung tetap 21× melebihi kapasitas ilmiah. Monitoring tanpa penegakan adalah tata kelola performatif.',
      stationTypes: {
        primary: 'Stasiun Danau Primer',
        secondary: 'Stasiun Anak Sungai'
      },
      statusLabels: {
        hypereutrophic: 'Hypereutrophic (Kritis)',
        eutrophic: 'Eutrophic (Tinggi)',
        mesotrophic: 'Mesotrophic (Sedang)'
      },
      metrics: {
        tsi: 'Indeks Status Trofik',
        phosphate: 'Fosfat (mg/L)',
        hyacinth: 'Cakupan Eceng Gondok (ha)',
        stations: 'Stasiun Monitoring'
      },
      actions: {
        current: 'Hanya pengumpulan data',
        ideal: 'Penegakan real-time'
      }
    }
  }

  const t = content[language]

  // Load data
  useEffect(() => {
    if (!lakeId) return

    Promise.all([
      fetch(`/data/lakes/${lakeId}/monitoring-stations.json`).then(res => res.json()),
      fetch(`/data/lakes/${lakeId}/water-quality-trends.json`).then(res => res.json())
    ])
      .then(([stationsData, trendsData]) => {
        setStations(stationsData)
        setTrends(trendsData)
      })
      .catch(err => console.error('Error loading environmental data:', err))
  }, [lakeId])

  // Create monitoring map
  useEffect(() => {
    if (stations.length === 0) return

    if (compareMode) {
      createMonitoringMap(currentMapRef.current, stations, 'current')
      createMonitoringMap(idealMapRef.current, stations, 'ideal')
    } else {
      createMonitoringMap(currentMapRef.current, stations, 'current')
    }
  }, [stations, language, compareMode])

  // Create trend charts
  useEffect(() => {
    if (trends.length === 0) return

    createTSITrend(trendRef.current, trends)
    createHyacinthTrend(hyacinthRef.current, trends)
  }, [trends, language])

  function createMonitoringMap(container, data, state) {
    if (!container) return

    d3.select(container).selectAll('*').remove()

    const width = 600
    const height = 500
    const svg = d3.select(container)
      .attr('width', width)
      .attr('height', height)

    // Lake outline (simplified ellipse)
    svg.append('ellipse')
      .attr('cx', width / 2)
      .attr('cy', height / 2)
      .attr('rx', 200)
      .attr('ry', 150)
      .attr('fill', state === 'current' ? '#e8f4f8' : '#d4f4dd')
      .attr('stroke', state === 'current' ? '#2989d8' : '#51cf66')
      .attr('stroke-width', 2)

    // Title
    svg.append('text')
      .attr('x', width / 2)
      .attr('y', 30)
      .attr('text-anchor', 'middle')
      .attr('font-size', '14px')
      .attr('font-weight', 'bold')
      .attr('fill', '#1a1a2e')
      .text(state === 'current' ? `${data.length} ${t.metrics.stations}` : t.actions.ideal)

    // Monitoring stations
    data.forEach((station, i) => {
      const isPrimary = station.type === 'primary'
      const statusColor = station.latest_status === 'hypereutrophic' ? '#ff6b6b' :
                         station.latest_status === 'eutrophic' ? '#ffcc00' : '#51cf66'

      // Station marker
      const group = svg.append('g')
        .attr('transform', `translate(${station.coordinates.x}, ${station.coordinates.y})`)

      group.append('circle')
        .attr('r', isPrimary ? 8 : 5)
        .attr('fill', statusColor)
        .attr('stroke', '#ffffff')
        .attr('stroke-width', 2)

      // Station label (only for primary in current, all in ideal)
      if (isPrimary || state === 'ideal') {
        group.append('text')
          .attr('x', 0)
          .attr('y', isPrimary ? -12 : -8)
          .attr('text-anchor', 'middle')
          .attr('font-size', '8px')
          .attr('fill', '#1a1a2e')
          .text(station.name.split(' - ')[0])
      }

      // Ideal state: add alert zones and response connections
      if (state === 'ideal' && isPrimary) {
        // Alert zone radius
        group.append('circle')
          .attr('r', 30)
          .attr('fill', 'none')
          .attr('stroke', '#51cf66')
          .attr('stroke-width', 1)
          .attr('stroke-dasharray', '3,3')
          .attr('opacity', 0.5)

        // Connection to response system (converging to center)
        svg.append('line')
          .attr('x1', station.coordinates.x)
          .attr('y1', station.coordinates.y)
          .attr('x2', width / 2)
          .attr('y2', height / 2)
          .attr('stroke', '#51cf66')
          .attr('stroke-width', 1)
          .attr('stroke-dasharray', '2,2')
          .attr('opacity', 0.3)
      }
    })

    // Current state: add "No enforcement linkage" text
    if (state === 'current') {
      svg.append('text')
        .attr('x', width / 2)
        .attr('y', height - 20)
        .attr('text-anchor', 'middle')
        .attr('font-size', '11px')
        .attr('fill', '#ff6b6b')
        .attr('font-weight', 'bold')
        .text(t.actions.current.toUpperCase())
    } else {
      // Ideal state: central response hub
      svg.append('circle')
        .attr('cx', width / 2)
        .attr('cy', height / 2)
        .attr('r', 40)
        .attr('fill', '#51cf66')
        .attr('fill-opacity', 0.3)
        .attr('stroke', '#51cf66')
        .attr('stroke-width', 2)

      svg.append('text')
        .attr('x', width / 2)
        .attr('y', height / 2)
        .attr('text-anchor', 'middle')
        .attr('font-size', '10px')
        .attr('fill', '#1a1a2e')
        .attr('font-weight', 'bold')
        .text('Response Hub')
    }

    // Legend
    const legendData = [
      { label: t.statusLabels.hypereutrophic, color: '#ff6b6b' },
      { label: t.statusLabels.eutrophic, color: '#ffcc00' },
      { label: t.statusLabels.mesotrophic, color: '#51cf66' }
    ]

    legendData.forEach((item, i) => {
      const legendY = height - 80 + (i * 20)

      svg.append('circle')
        .attr('cx', 30)
        .attr('cy', legendY)
        .attr('r', 5)
        .attr('fill', item.color)

      svg.append('text')
        .attr('x', 40)
        .attr('y', legendY + 4)
        .attr('font-size', '9px')
        .attr('fill', '#1a1a2e')
        .text(item.label)
    })
  }

  function createTSITrend(container, data) {
    if (!container) return

    d3.select(container).selectAll('*').remove()

    const width = 700
    const height = 300
    const margin = { top: 20, right: 40, bottom: 40, left: 60 }
    const innerWidth = width - margin.left - margin.right
    const innerHeight = height - margin.top - margin.bottom

    const svg = d3.select(container)
      .attr('width', width)
      .attr('height', height)

    const g = svg.append('g')
      .attr('transform', `translate(${margin.left},${margin.top})`)

    // Scales
    const xScale = d3.scaleLinear()
      .domain([2020, 2024])
      .range([0, innerWidth])

    const yScale = d3.scaleLinear()
      .domain([50, 80])
      .range([innerHeight, 0])

    // Axes
    g.append('g')
      .attr('transform', `translate(0,${innerHeight})`)
      .call(d3.axisBottom(xScale).tickFormat(d3.format('d')))

    g.append('g')
      .call(d3.axisLeft(yScale))

    // Hypereutrophic threshold line (TSI > 70)
    g.append('line')
      .attr('x1', 0)
      .attr('x2', innerWidth)
      .attr('y1', yScale(70))
      .attr('y2', yScale(70))
      .attr('stroke', '#ff6b6b')
      .attr('stroke-width', 2)
      .attr('stroke-dasharray', '5,5')

    g.append('text')
      .attr('x', innerWidth - 10)
      .attr('y', yScale(70) - 5)
      .attr('text-anchor', 'end')
      .attr('font-size', '10px')
      .attr('fill', '#ff6b6b')
      .attr('font-weight', 'bold')
      .text('Hypereutrophic Threshold (TSI 70)')

    // TSI line
    const line = d3.line()
      .x(d => xScale(d.year))
      .y(d => yScale(d.tsi_average))

    g.append('path')
      .datum(data)
      .attr('d', line)
      .attr('fill', 'none')
      .attr('stroke', '#2989d8')
      .attr('stroke-width', 3)

    // Data points
    g.selectAll('.tsi-point')
      .data(data)
      .enter()
      .append('circle')
      .attr('class', 'tsi-point')
      .attr('cx', d => xScale(d.year))
      .attr('cy', d => yScale(d.tsi_average))
      .attr('r', 5)
      .attr('fill', d => d.tsi_average > 70 ? '#ff6b6b' : '#ffcc00')
      .attr('stroke', '#ffffff')
      .attr('stroke-width', 2)

    // Labels
    g.append('text')
      .attr('x', innerWidth / 2)
      .attr('y', innerHeight + 35)
      .attr('text-anchor', 'middle')
      .attr('font-size', '11px')
      .attr('fill', '#1a1a2e')
      .text('Year')

    g.append('text')
      .attr('x', -innerHeight / 2)
      .attr('y', -45)
      .attr('text-anchor', 'middle')
      .attr('transform', 'rotate(-90)')
      .attr('font-size', '11px')
      .attr('fill', '#1a1a2e')
      .text(t.metrics.tsi)
  }

  function createHyacinthTrend(container, data) {
    if (!container) return

    d3.select(container).selectAll('*').remove()

    const width = 700
    const height = 300
    const margin = { top: 20, right: 40, bottom: 40, left: 60 }
    const innerWidth = width - margin.left - margin.right
    const innerHeight = height - margin.top - margin.bottom

    const svg = d3.select(container)
      .attr('width', width)
      .attr('height', height)

    const g = svg.append('g')
      .attr('transform', `translate(${margin.left},${margin.top})`)

    // Scales
    const xScale = d3.scaleLinear()
      .domain([2020, 2024])
      .range([0, innerWidth])

    const yScale = d3.scaleLinear()
      .domain([0, 300])
      .range([innerHeight, 0])

    // Axes
    g.append('g')
      .attr('transform', `translate(0,${innerHeight})`)
      .call(d3.axisBottom(xScale).tickFormat(d3.format('d')))

    g.append('g')
      .call(d3.axisLeft(yScale))

    // Area chart
    const area = d3.area()
      .x(d => xScale(d.year))
      .y0(innerHeight)
      .y1(d => yScale(d.hyacinth_coverage_hectares))

    g.append('path')
      .datum(data)
      .attr('d', area)
      .attr('fill', '#51cf66')
      .attr('fill-opacity', 0.4)

    // Line
    const line = d3.line()
      .x(d => xScale(d.year))
      .y(d => yScale(d.hyacinth_coverage_hectares))

    g.append('path')
      .datum(data)
      .attr('d', line)
      .attr('fill', 'none')
      .attr('stroke', '#51cf66')
      .attr('stroke-width', 3)

    // Data points with labels
    g.selectAll('.hyacinth-point')
      .data(data)
      .enter()
      .append('circle')
      .attr('class', 'hyacinth-point')
      .attr('cx', d => xScale(d.year))
      .attr('cy', d => yScale(d.hyacinth_coverage_hectares))
      .attr('r', 5)
      .attr('fill', '#51cf66')
      .attr('stroke', '#ffffff')
      .attr('stroke-width', 2)

    // Value labels
    g.selectAll('.hyacinth-label')
      .data(data)
      .enter()
      .append('text')
      .attr('class', 'hyacinth-label')
      .attr('x', d => xScale(d.year))
      .attr('y', d => yScale(d.hyacinth_coverage_hectares) - 10)
      .attr('text-anchor', 'middle')
      .attr('font-size', '10px')
      .attr('fill', '#1a1a2e')
      .attr('font-weight', 'bold')
      .text(d => `${d.hyacinth_coverage_hectares} ha`)

    // Axes labels
    g.append('text')
      .attr('x', innerWidth / 2)
      .attr('y', innerHeight + 35)
      .attr('text-anchor', 'middle')
      .attr('font-size', '11px')
      .attr('fill', '#1a1a2e')
      .text('Year')

    g.append('text')
      .attr('x', -innerHeight / 2)
      .attr('y', -45)
      .attr('text-anchor', 'middle')
      .attr('transform', 'rotate(-90)')
      .attr('font-size', '11px')
      .attr('fill', '#1a1a2e')
      .text(t.metrics.hyacinth)
  }

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl font-bold text-lake-dark">{t.title}</h1>
        <p className="text-lg text-urgency-red font-semibold mt-2">{t.subtitle}</p>
        <p className="text-gray-700 mt-2 max-w-4xl">{t.description}</p>
      </div>

      {compareMode ? (
        <div className="grid grid-cols-2 gap-6">
          <div className="module-card current-state">
            <h3 className="text-xl font-bold text-urgency-red mb-2">{t.currentTitle}</h3>
            <p className="text-sm text-gray-600 mb-4">{t.currentDescription}</p>
            <svg ref={currentMapRef}></svg>
          </div>
          <div className="module-card ideal-state">
            <h3 className="text-xl font-bold text-hope-green mb-2">{t.idealTitle}</h3>
            <p className="text-sm text-gray-600 mb-4">{t.idealDescription}</p>
            <svg ref={idealMapRef}></svg>
          </div>
        </div>
      ) : (
        <div className="module-card current-state">
          <h3 className="text-xl font-bold text-urgency-red mb-2">{t.currentTitle}</h3>
          <p className="text-sm text-gray-600 mb-4">{t.currentDescription}</p>
          <svg ref={currentMapRef}></svg>
        </div>
      )}

      <div className="module-card bg-blue-50 border-water-blue">
        <h3 className="text-xl font-bold text-lake-dark mb-2">{t.trendTitle}</h3>
        <p className="text-sm text-gray-600 mb-4">{t.trendDescription}</p>
        <div className="flex justify-center">
          <svg ref={trendRef}></svg>
        </div>
      </div>

      <div className="module-card bg-green-50 border-hope-green">
        <h3 className="text-xl font-bold text-lake-dark mb-2">{t.hyacinthTitle}</h3>
        <p className="text-sm text-gray-600 mb-4">{t.hyacinthDescription}</p>
        <div className="flex justify-center">
          <svg ref={hyacinthRef}></svg>
        </div>
      </div>

      <div className="module-card bg-red-50 border-urgency-red">
        <h3 className="text-lg font-bold text-urgency-red mb-2">🔍 {t.keyFinding}</h3>
        <p className="text-gray-800">{t.keyFindingText}</p>
      </div>
    </div>
  )
}

export default EnvironmentalMonitoring
