import { useEffect, useRef, useState } from 'react'
import { useOutletContext } from 'react-router-dom'
import * as d3 from 'd3'

function GovernanceNetwork() {
  const { language, compareMode, lakeId } = useOutletContext()
  const currentSvgRef = useRef()
  const idealSvgRef = useRef()
  const [stakeholders, setStakeholders] = useState([])
  const [selectedNode, setSelectedNode] = useState(null)

  // Load stakeholder data
  useEffect(() => {
    if (!lakeId) return

    fetch(`/data/lakes/${lakeId}/stakeholders.json`)
      .then(res => res.json())
      .then(data => setStakeholders(data))
      .catch(err => console.error('Error loading stakeholders:', err))
  }, [lakeId])

  // Create network visualization
  useEffect(() => {
    if (stakeholders.length === 0) return

    // Current State Network
    if (currentSvgRef.current) {
      createNetwork(currentSvgRef.current, stakeholders, 'current')
    }

    // Ideal State Network
    if (compareMode && idealSvgRef.current) {
      createNetwork(idealSvgRef.current, stakeholders, 'ideal')
    }
  }, [stakeholders, compareMode])

  function createNetwork(container, data, state) {
    // Clear previous visualization
    d3.select(container).selectAll('*').remove()

    const width = container.clientWidth || 600
    const height = 500

    const svg = d3.select(container)
      .attr('width', width)
      .attr('height', height)
      .attr('viewBox', [0, 0, width, height])

    // Create network data structure
    const nodes = data.map(s => ({
      id: s.id,
      name: s.name,
      code: s.code,
      org: s.organization,
      tier: s.tier,
      authorityLevel: s.authority_level,
      x: width / 2,
      y: height / 2
    }))

    // Add special nodes for current state
    if (state === 'current') {
      // Add BPN node (excluded)
      nodes.push({
        id: 'stakeholder-bpn',
        name: 'BPN',
        code: 'BPN',
        org: 'National Land Agency',
        tier: 'national',
        authorityLevel: 8,
        excluded: true,
        x: width * 0.85,
        y: height * 0.2
      })

      // Add Pokja node
      nodes.push({
        id: 'coordination-pokja',
        name: 'Pokja Rawa Pening',
        code: 'POKJA',
        org: 'Coordination Forum',
        tier: 'coordination',
        authorityLevel: 6,
        x: width / 2,
        y: height / 2
      })
    } else {
      // Ideal state: BPN integrated
      nodes.push({
        id: 'stakeholder-bpn',
        name: 'BPN',
        code: 'BPN',
        org: 'National Land Agency',
        tier: 'national',
        authorityLevel: 8,
        integrated: true,
        x: width * 0.6,
        y: height * 0.4
      })

      nodes.push({
        id: 'coordination-hub',
        name: 'Integrated Coordination Hub',
        code: 'HUB',
        org: 'Multi-stakeholder Platform',
        tier: 'coordination',
        authorityLevel: 7,
        x: width / 2,
        y: height / 2
      })
    }

    // Create links
    const links = []
    const centerNode = state === 'current' ? 'coordination-pokja' : 'coordination-hub'

    nodes.forEach(node => {
      if (node.id !== centerNode) {
        if (state === 'current') {
          // Weak or broken links in current state
          if (node.id === 'stakeholder-bpn') {
            // BPN disconnected
            links.push({
              source: node.id,
              target: centerNode,
              strength: 0,
              broken: true
            })
          } else if (node.tier === 'community') {
            // Community marginalized
            links.push({
              source: node.id,
              target: centerNode,
              strength: 0.3
            })
          } else {
            // Government agencies connected
            links.push({
              source: node.id,
              target: centerNode,
              strength: 0.7
            })
          }
        } else {
          // Strong links in ideal state
          links.push({
            source: node.id,
            target: centerNode,
            strength: 1
          })
        }
      }
    })

    // Color scale by tier
    const tierColors = {
      national: '#007acc',    // Trust Blue
      provincial: '#2989d8',  // Water Blue
      local: '#51cf66',       // Hope Green
      community: '#ff6b6b',   // Urgency Red (needs attention)
      coordination: '#9d4edd' // Purple (central)
    }

    // Create force simulation
    const simulation = d3.forceSimulation(nodes)
      .force('link', d3.forceLink(links).id(d => d.id).distance(100).strength(d => d.strength))
      .force('charge', d3.forceManyBody().strength(-300))
      .force('center', d3.forceCenter(width / 2, height / 2))
      .force('collision', d3.forceCollide().radius(40))

    // Draw links
    const link = svg.append('g')
      .selectAll('line')
      .data(links)
      .join('line')
      .attr('stroke', d => d.broken ? '#ff6b6b' : '#999')
      .attr('stroke-width', d => d.broken ? 2 : (d.strength * 3))
      .attr('stroke-dasharray', d => d.broken ? '5,5' : 'none')
      .attr('stroke-opacity', d => d.broken ? 0.3 : 0.6)

    // Draw nodes
    const node = svg.append('g')
      .selectAll('g')
      .data(nodes)
      .join('g')
      .attr('cursor', 'pointer')
      .call(drag(simulation))
      .on('click', (event, d) => {
        setSelectedNode(d)
      })

    // Node circles
    node.append('circle')
      .attr('r', d => {
        if (d.tier === 'coordination') return 25
        return 8 + (d.authorityLevel * 2)
      })
      .attr('fill', d => tierColors[d.tier] || '#999')
      .attr('stroke', d => {
        if (d.excluded) return '#ff6b6b'
        if (d.integrated) return '#51cf66'
        return '#fff'
      })
      .attr('stroke-width', d => (d.excluded || d.integrated) ? 3 : 2)
      .attr('opacity', d => d.excluded ? 0.5 : 1)

    // Node labels
    node.append('text')
      .text(d => d.code)
      .attr('text-anchor', 'middle')
      .attr('dy', d => (d.tier === 'coordination' ? 35 : 25))
      .attr('font-size', d => (d.tier === 'coordination' ? '12px' : '10px'))
      .attr('font-weight', d => (d.tier === 'coordination' ? 'bold' : 'normal'))
      .attr('fill', '#333')

    // Annotations for special nodes
    if (state === 'current') {
      const bpnNode = nodes.find(n => n.id === 'stakeholder-bpn')
      if (bpnNode) {
        svg.append('text')
          .attr('x', width * 0.85)
          .attr('y', height * 0.15)
          .attr('text-anchor', 'middle')
          .attr('font-size', '11px')
          .attr('fill', '#ff6b6b')
          .attr('font-weight', 'bold')
          .text('EXCLUDED')

        svg.append('text')
          .attr('x', width * 0.85)
          .attr('y', height * 0.3)
          .attr('text-anchor', 'middle')
          .attr('font-size', '9px')
          .attr('fill', '#666')
          .text('Controls 4,292 parcels')
      }
    }

    // Update positions on simulation tick
    simulation.on('tick', () => {
      link
        .attr('x1', d => d.source.x)
        .attr('y1', d => d.source.y)
        .attr('x2', d => d.target.x)
        .attr('y2', d => d.target.y)

      node.attr('transform', d => `translate(${d.x},${d.y})`)
    })
  }

  function drag(simulation) {
    function dragstarted(event) {
      if (!event.active) simulation.alphaTarget(0.3).restart()
      event.subject.fx = event.subject.x
      event.subject.fy = event.subject.y
    }

    function dragged(event) {
      event.subject.fx = event.x
      event.subject.fy = event.y
    }

    function dragended(event) {
      if (!event.active) simulation.alphaTarget(0)
      event.subject.fx = null
      event.subject.fy = null
    }

    return d3.drag()
      .on('start', dragstarted)
      .on('drag', dragged)
      .on('end', dragended)
  }

  const content = {
    en: {
      title: 'Governance Network',
      subtitle: 'Stakeholder Relationships & Coordination Structure',
      currentLabel: 'Current Fragmented State',
      idealLabel: 'Integrated Ideal State',
      description: 'This network visualization exposes governance fragmentation in Rawa Pening lake management. The current state shows BPN (National Land Agency) excluded from coordination despite controlling 4,292 land parcels, and community stakeholders marginalized with weak connections. The ideal state demonstrates integrated coordination with all critical actors participating.',
      bpnExclusion: 'BPN Exclusion Crisis',
      bpnDesc: 'The National Land Agency controls all land data (4,292+ parcels) but operates outside the Pokja coordination structure. This institutional exclusion makes land acquisition planning impossible.',
      legend: 'Legend'
    },
    id: {
      title: 'Jaringan Tata Kelola',
      subtitle: 'Hubungan Pemangku Kepentingan & Struktur Koordinasi',
      currentLabel: 'Kondisi Terfragmentasi Saat Ini',
      idealLabel: 'Kondisi Ideal Terintegrasi',
      description: 'Visualisasi jaringan ini mengungkap fragmentasi tata kelola dalam pengelolaan Danau Rawa Pening. Kondisi saat ini menunjukkan BPN (Badan Pertanahan Nasional) dikecualikan dari koordinasi meskipun mengendalikan 4.292 bidang tanah, dan pemangku kepentingan masyarakat terpinggirkan dengan koneksi lemah. Kondisi ideal mendemonstrasikan koordinasi terintegrasi dengan semua aktor kritis berpartisipasi.',
      bpnExclusion: 'Krisis Pengecualian BPN',
      bpnDesc: 'Badan Pertanahan Nasional mengendalikan semua data tanah (4.292+ bidang) tetapi beroperasi di luar struktur koordinasi Pokja. Pengecualian institusional ini membuat perencanaan akuisisi lahan mustahil.',
      legend: 'Legenda'
    }
  }

  const t = content[language]

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-3xl font-bold text-lake-dark">{t.title}</h1>
        <p className="text-gray-600 mt-2">{t.subtitle}</p>
      </div>

      {/* Description */}
      <div className="module-card">
        <p className="text-gray-700">{t.description}</p>
      </div>

      {/* Network Visualizations */}
      <div className={`grid ${compareMode ? 'grid-cols-2' : 'grid-cols-1'} gap-6`}>
        {/* Current State */}
        <div className="module-card current-state">
          <h3 className="text-lg font-semibold text-urgency-red mb-4">{t.currentLabel}</h3>
          <svg ref={currentSvgRef} className="w-full border border-gray-200 rounded"></svg>
          <div className="mt-4 p-3 bg-red-50 border-l-4 border-urgency-red">
            <h4 className="font-semibold text-sm">{t.bpnExclusion}</h4>
            <p className="text-sm text-gray-700 mt-1">{t.bpnDesc}</p>
          </div>
        </div>

        {/* Ideal State */}
        {compareMode && (
          <div className="module-card ideal-state">
            <h3 className="text-lg font-semibold text-hope-green mb-4">{t.idealLabel}</h3>
            <svg ref={idealSvgRef} className="w-full border border-gray-200 rounded"></svg>
            <div className="mt-4 p-3 bg-green-50 border-l-4 border-hope-green">
              <h4 className="font-semibold text-sm">BPN Integrated</h4>
              <p className="text-sm text-gray-700 mt-1">
                National Land Agency fully integrated into coordination hub with real-time data sharing protocols. Community feedback loops operational.
              </p>
            </div>
          </div>
        )}
      </div>

      {/* Legend */}
      <div className="module-card">
        <h3 className="text-lg font-semibold mb-4">{t.legend}</h3>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          <div className="flex items-center space-x-2">
            <div className="w-6 h-6 rounded-full bg-trust-blue"></div>
            <span className="text-sm">National Tier</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-6 h-6 rounded-full bg-water-blue"></div>
            <span className="text-sm">Provincial Tier</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-6 h-6 rounded-full bg-hope-green"></div>
            <span className="text-sm">Local Tier</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-6 h-6 rounded-full bg-urgency-red"></div>
            <span className="text-sm">Community</span>
          </div>
        </div>
      </div>

      {/* Selected Node Detail */}
      {selectedNode && (
        <div className="module-card bg-blue-50">
          <h3 className="text-lg font-semibold mb-2">Selected: {selectedNode.name}</h3>
          <p className="text-sm text-gray-700"><strong>Organization:</strong> {selectedNode.org}</p>
          <p className="text-sm text-gray-700"><strong>Tier:</strong> {selectedNode.tier}</p>
          <p className="text-sm text-gray-700"><strong>Authority Level:</strong> {selectedNode.authorityLevel}/10</p>
          {selectedNode.excluded && (
            <p className="text-sm text-urgency-red font-semibold mt-2">⚠️ EXCLUDED FROM COORDINATION</p>
          )}
        </div>
      )}
    </div>
  )
}

export default GovernanceNetwork
