import { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'

function LandingPage() {
  const navigate = useNavigate()
  const [lakes, setLakes] = useState([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    fetch('/data/lakes/index.json')
      .then(res => res.json())
      .then(data => {
        setLakes(data.lakes)
        setLoading(false)
      })
      .catch(err => {
        console.error('Error loading lakes index:', err)
        setLoading(false)
      })
  }, [])

  const statusColors = {
    complete: 'bg-hope-green',
    placeholder: 'bg-gray-400'
  }

  const statusLabels = {
    complete: 'Data Available',
    placeholder: 'Planned'
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-lake-light flex items-center justify-center">
        <div className="text-xl text-lake-dark">Loading lakes...</div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-lake-light">
      <div className="max-w-7xl mx-auto px-4 py-12">
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold text-lake-dark mb-4">
            Indonesian Priority Lakes Governance System
          </h1>
          <p className="text-xl text-gray-700 mb-2">
            Multi-Lake Environmental Governance Information System
          </p>
          <p className="text-gray-600">
            CSIRO Lake Governance Research - Interactive Coordination Dashboard
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-12">
          {lakes.map(lake => (
            <div
              key={lake.id}
              onClick={() => navigate(`/${lake.id}`)}
              className={`
                bg-white rounded-lg shadow-lg p-6 cursor-pointer
                transform transition-all hover:scale-105 hover:shadow-xl
                ${lake.status === 'placeholder' ? 'opacity-60' : ''}
              `}
            >
              <div className="flex items-start justify-between mb-3">
                <h2 className="text-2xl font-bold text-lake-dark">
                  {lake.name.en}
                </h2>
                <div className={`
                  ${statusColors[lake.status]}
                  text-white text-xs px-2 py-1 rounded-full
                `}>
                  {statusLabels[lake.status]}
                </div>
              </div>

              <div className="text-sm text-gray-600 mb-4">
                <div className="flex items-center mb-1">
                  <span className="font-semibold mr-2">Region:</span>
                  <span>{lake.region}</span>
                </div>
                <div className="flex items-center mb-1">
                  <span className="font-semibold mr-2">Fieldwork:</span>
                  <span>{lake.fieldwork_date}</span>
                </div>
                <div className="flex items-center">
                  <span className="font-semibold mr-2">Modules:</span>
                  <span>{lake.modules_complete} of 7</span>
                </div>
              </div>

              {lake.status === 'complete' ? (
                <button className="w-full bg-water-blue text-white py-2 rounded-lg hover:bg-opacity-90 transition-colors">
                  Explore Dashboard →
                </button>
              ) : (
                <button
                  className="w-full bg-gray-300 text-gray-600 py-2 rounded-lg cursor-not-allowed"
                  disabled
                >
                  Coming Soon
                </button>
              )}
            </div>
          ))}
        </div>

        <div className="bg-white rounded-lg shadow-lg p-8 text-center">
          <h3 className="text-2xl font-bold text-lake-dark mb-4">
            Cross-Lake Analysis
          </h3>
          <p className="text-gray-600 mb-6">
            Compare governance patterns, institutional paradoxes, and environmental justice issues across multiple lakes
          </p>
          <button
            className="bg-gray-300 text-gray-600 px-8 py-3 rounded-lg cursor-not-allowed"
            disabled
          >
            Compare Lakes (Coming Soon)
          </button>
        </div>

        <div className="mt-12 text-center text-sm text-gray-500">
          <p>CSIRO Lake Governance Research Project</p>
          <p className="mt-1">Evidence-Based Environmental Governance Analysis</p>
        </div>
      </div>
    </div>
  )
}

export default LandingPage
