import { useEffect, useRef, useState } from 'react'
import { useOutletContext } from 'react-router-dom'
import * as d3 from 'd3'

function InfoSystemsInfra() {
  const { language, compareMode, lakeId } = useOutletContext()
  const currentRef = useRef(null)
  const idealRef = useRef(null)
  const timelineRef = useRef(null)
  const [systems, setSystems] = useState([])

  // Bilingual content
  const content = {
    en: {
      title: 'Information Systems Infrastructure',
      subtitle: 'Exposing Data Fragmentation & SIMOLEK Orphaning',
      description: 'This visualization reveals how institutional reorganization orphaned a 3 billion rupiah monitoring system (SIMOLEK), while critical land parcel data (4,292 parcels) remains inaccessible to coordination bodies. Five data systems operate in isolation, creating information silos that prevent evidence-based governance.',
      currentTitle: 'Current: Fragmented Data Silos',
      idealTitle: 'Ideal: Integrated Knowledge Hub',
      currentDescription: 'SIMOLEK orphaned after BPDAS reorganization (2023). BPN land data (4,292 parcels) excluded from Pokja. Each agency maintains isolated systems with no data sharing protocols.',
      idealDescription: 'SIMOLEK revived as central coordination hub. BPN integrated with real-time data sharing. Standardized APIs enable cross-agency analysis and accountability tracking.',
      timelineTitle: 'Data-Action Paradox Timeline (2020-2024)',
      timelineDescription: 'Dense monitoring activity produces data, but minimal management action follows. This gap reveals governance failure, not information deficit.',
      keyFinding: 'SIMOLEK Orphaning: A Governance Microcosm',
      keyFindingText: 'The 3B rupiah SIMOLEK system was commissioned by BPDAS in 2023 but became orphaned when BPDAS lost lake management authority later that year. No agency claimed ownership. The system remains inaccessible. This is not a technical failure—it is institutional dysfunction.',
      systems: {
        'system-simolek': 'SIMOLEK (Orphaned 2023)',
        'system-dlh-monitoring': 'DLH Monitoring (29 stations)',
        'system-bws-aerial': 'BWS Aerial Photos',
        'system-bpn-land': 'BPN Land Database (INACCESSIBLE)',
        'system-research': 'Academic Research'
      },
      pokja: 'Pokja Coordination Hub',
      bpdas: 'BPDAS (Authority Lost 2023)',
      integrated: 'Integrated Data Platform',
      status: {
        orphaned: 'ORPHANED',
        active: 'ACTIVE',
        isolated: 'ISOLATED',
        excluded: 'EXCLUDED'
      },
      timeline: {
        monitoring: 'Monitoring Activity',
        action: 'Management Action',
        gap: 'DATA-ACTION GAP'
      }
    },
    id: {
      title: 'Infrastruktur Sistem Informasi',
      subtitle: 'Mengungkap Fragmentasi Data & SIMOLEK Terlantar',
      description: 'Visualisasi ini mengungkapkan bagaimana reorganisasi institusional menyebabkan sistem monitoring senilai 3 miliar rupiah (SIMOLEK) terlantar, sementara data parsel lahan kritis (4.292 parsel) tetap tidak dapat diakses oleh badan koordinasi. Lima sistem data beroperasi secara terpisah, menciptakan silo informasi yang mencegah tata kelola berbasis bukti.',
      currentTitle: 'Saat Ini: Silo Data Terfragmentasi',
      idealTitle: 'Ideal: Hub Pengetahuan Terintegrasi',
      currentDescription: 'SIMOLEK terlantar setelah reorganisasi BPDAS (2023). Data lahan BPN (4.292 parsel) dikecualikan dari Pokja. Setiap instansi mempertahankan sistem terisolasi tanpa protokol berbagi data.',
      idealDescription: 'SIMOLEK dihidupkan kembali sebagai hub koordinasi pusat. BPN terintegrasi dengan berbagi data real-time. API terstandar memungkinkan analisis lintas-instansi dan pelacakan akuntabilitas.',
      timelineTitle: 'Timeline Paradoks Data-Aksi (2020-2024)',
      timelineDescription: 'Aktivitas monitoring padat menghasilkan data, tetapi aksi manajemen minimal. Kesenjangan ini mengungkap kegagalan tata kelola, bukan defisit informasi.',
      keyFinding: 'SIMOLEK Terlantar: Mikrokosmos Tata Kelola',
      keyFindingText: 'Sistem SIMOLEK senilai 3 miliar rupiah ditugaskan oleh BPDAS pada 2023 tetapi terlantar ketika BPDAS kehilangan kewenangan pengelolaan danau di tahun yang sama. Tidak ada instansi yang mengklaim kepemilikan. Sistem tetap tidak dapat diakses. Ini bukan kegagalan teknis—ini adalah disfungsi institusional.',
      systems: {
        'system-simolek': 'SIMOLEK (Terlantar 2023)',
        'system-dlh-monitoring': 'Monitoring DLH (29 stasiun)',
        'system-bws-aerial': 'Foto Udara BWS',
        'system-bpn-land': 'Database Lahan BPN (TIDAK DAPAT DIAKSES)',
        'system-research': 'Penelitian Akademis'
      },
      pokja: 'Pokja Hub Koordinasi',
      bpdas: 'BPDAS (Kehilangan Kewenangan 2023)',
      integrated: 'Platform Data Terintegrasi',
      status: {
        orphaned: 'TERLANTAR',
        active: 'AKTIF',
        isolated: 'TERISOLASI',
        excluded: 'DIKECUALIKAN'
      },
      timeline: {
        monitoring: 'Aktivitas Monitoring',
        action: 'Aksi Manajemen',
        gap: 'KESENJANGAN DATA-AKSI'
      }
    }
  }

  const t = content[language]

  // Load data systems
  useEffect(() => {
    if (!lakeId) return

    fetch(`/data/lakes/${lakeId}/data-systems.json`)
      .then(res => res.json())
      .then(data => setSystems(data))
      .catch(err => console.error('Error loading data systems:', err))
  }, [lakeId])

  // Create system architecture diagram
  useEffect(() => {
    if (systems.length === 0) return

    if (compareMode) {
      createArchitecture(currentRef.current, systems, 'current')
      createArchitecture(idealRef.current, systems, 'ideal')
    } else {
      createArchitecture(currentRef.current, systems, 'current')
    }
  }, [systems, language, compareMode])

  // Create data-action paradox timeline
  useEffect(() => {
    if (timelineRef.current) {
      createParadoxTimeline(timelineRef.current)
    }
  }, [language])

  function createArchitecture(container, data, state) {
    if (!container) return

    // Clear previous
    d3.select(container).selectAll('*').remove()

    const width = 600
    const height = 500
    const svg = d3.select(container)
      .attr('width', width)
      .attr('height', height)

    if (state === 'current') {
      // Current: Fragmented systems with SIMOLEK orphaned, BPN excluded
      const systemPositions = [
        { id: 'system-simolek', x: 300, y: 100, orphaned: true },
        { id: 'system-dlh-monitoring', x: 150, y: 250 },
        { id: 'system-bws-aerial', x: 450, y: 250 },
        { id: 'system-bpn-land', x: 150, y: 400, excluded: true },
        { id: 'system-research', x: 450, y: 400 }
      ]

      // Pokja coordination hub (weak center)
      svg.append('circle')
        .attr('cx', 300)
        .attr('cy', 300)
        .attr('r', 30)
        .attr('fill', '#2989d8')
        .attr('fill-opacity', 0.3)
        .attr('stroke', '#2989d8')
        .attr('stroke-width', 2)
        .attr('stroke-dasharray', '5,5')

      svg.append('text')
        .attr('x', 300)
        .attr('y', 305)
        .attr('text-anchor', 'middle')
        .attr('font-size', '10px')
        .attr('fill', '#1a1a2e')
        .text(t.pokja)

      // Draw weak connections (partial)
      const weakConnections = [
        { from: systemPositions[1], to: { x: 300, y: 300 } }, // DLH to Pokja
        { from: systemPositions[2], to: { x: 300, y: 300 } }  // BWS to Pokja
      ]

      weakConnections.forEach(conn => {
        svg.append('line')
          .attr('x1', conn.from.x)
          .attr('y1', conn.from.y)
          .attr('x2', conn.to.x)
          .attr('y2', conn.to.y)
          .attr('stroke', '#cccccc')
          .attr('stroke-width', 1)
          .attr('stroke-dasharray', '3,3')
      })

      // Broken connection from SIMOLEK to BPDAS (lost authority)
      svg.append('line')
        .attr('x1', 300)
        .attr('y1', 100)
        .attr('x2', 300)
        .attr('y2', 180)
        .attr('stroke', '#ff6b6b')
        .attr('stroke-width', 2)
        .attr('stroke-dasharray', '5,5')

      svg.append('text')
        .attr('x', 310)
        .attr('y', 140)
        .attr('font-size', '9px')
        .attr('fill', '#ff6b6b')
        .text(t.bpdas)

      // X mark on broken connection
      svg.append('text')
        .attr('x', 300)
        .attr('y', 145)
        .attr('text-anchor', 'middle')
        .attr('font-size', '20px')
        .attr('fill', '#ff6b6b')
        .text('✗')

      // Draw system nodes
      systemPositions.forEach(pos => {
        const system = data.find(s => s.id === pos.id)
        if (!system) return

        const isOrphaned = pos.orphaned
        const isExcluded = pos.excluded

        // System box
        svg.append('rect')
          .attr('x', pos.x - 60)
          .attr('y', pos.y - 25)
          .attr('width', 120)
          .attr('height', 50)
          .attr('fill', isOrphaned ? '#555555' : (isExcluded ? '#ff6b6b' : '#007acc'))
          .attr('fill-opacity', isOrphaned ? 0.3 : 0.8)
          .attr('stroke', isOrphaned ? '#ff6b6b' : (isExcluded ? '#ff0000' : '#ffffff'))
          .attr('stroke-width', isExcluded ? 3 : 2)
          .attr('rx', 5)

        // System name
        svg.append('text')
          .attr('x', pos.x)
          .attr('y', pos.y)
          .attr('text-anchor', 'middle')
          .attr('font-size', '9px')
          .attr('fill', '#ffffff')
          .text(t.systems[system.id])

        // Status badge
        let statusText = isOrphaned ? t.status.orphaned : (isExcluded ? t.status.excluded : t.status.isolated)
        svg.append('text')
          .attr('x', pos.x)
          .attr('y', pos.y + 12)
          .attr('text-anchor', 'middle')
          .attr('font-size', '7px')
          .attr('fill', isOrphaned ? '#ff6b6b' : (isExcluded ? '#ff0000' : '#ffcc00'))
          .attr('font-weight', 'bold')
          .text(statusText)
      })

    } else {
      // Ideal: Integrated hub with SIMOLEK as central coordinator
      const centralHub = { x: 300, y: 250, r: 50 }

      // Central integrated platform (SIMOLEK revived)
      svg.append('circle')
        .attr('cx', centralHub.x)
        .attr('cy', centralHub.y)
        .attr('r', centralHub.r)
        .attr('fill', '#51cf66')
        .attr('fill-opacity', 0.3)
        .attr('stroke', '#51cf66')
        .attr('stroke-width', 3)

      svg.append('text')
        .attr('x', centralHub.x)
        .attr('y', centralHub.y - 5)
        .attr('text-anchor', 'middle')
        .attr('font-size', '11px')
        .attr('font-weight', 'bold')
        .attr('fill', '#1a1a2e')
        .text(t.systems['system-simolek'].replace(' (Orphaned 2023)', '').replace(' (Terlantar 2023)', ''))

      svg.append('text')
        .attr('x', centralHub.x)
        .attr('y', centralHub.y + 10)
        .attr('text-anchor', 'middle')
        .attr('font-size', '9px')
        .attr('fill', '#1a1a2e')
        .text(t.integrated)

      // Surrounding systems
      const systemPositions = [
        { id: 'system-dlh-monitoring', angle: 0 },
        { id: 'system-bws-aerial', angle: Math.PI / 2 },
        { id: 'system-bpn-land', angle: Math.PI },
        { id: 'system-research', angle: 3 * Math.PI / 2 }
      ]

      const radius = 180

      systemPositions.forEach(pos => {
        const system = data.find(s => s.id === pos.id)
        if (!system) return

        const x = centralHub.x + radius * Math.cos(pos.angle)
        const y = centralHub.y + radius * Math.sin(pos.angle)

        // Strong connection to central hub
        svg.append('line')
          .attr('x1', centralHub.x + centralHub.r * Math.cos(pos.angle))
          .attr('y1', centralHub.y + centralHub.r * Math.sin(pos.angle))
          .attr('x2', x)
          .attr('y2', y)
          .attr('stroke', '#51cf66')
          .attr('stroke-width', 3)

        // Bidirectional arrow
        const arrowMidX = (centralHub.x + x) / 2
        const arrowMidY = (centralHub.y + y) / 2
        svg.append('text')
          .attr('x', arrowMidX)
          .attr('y', arrowMidY)
          .attr('text-anchor', 'middle')
          .attr('font-size', '12px')
          .attr('fill', '#51cf66')
          .text('⇄')

        // System box
        svg.append('rect')
          .attr('x', x - 60)
          .attr('y', y - 25)
          .attr('width', 120)
          .attr('height', 50)
          .attr('fill', '#007acc')
          .attr('fill-opacity', 0.9)
          .attr('stroke', '#51cf66')
          .attr('stroke-width', 2)
          .attr('rx', 5)

        // System name
        let displayName = t.systems[system.id]
          .replace(' (INACCESSIBLE)', '')
          .replace(' (TIDAK DAPAT DIAKSES)', '')
        svg.append('text')
          .attr('x', x)
          .attr('y', y)
          .attr('text-anchor', 'middle')
          .attr('font-size', '9px')
          .attr('fill', '#ffffff')
          .text(displayName)

        // Active badge
        svg.append('text')
          .attr('x', x)
          .attr('y', y + 12)
          .attr('text-anchor', 'middle')
          .attr('font-size', '7px')
          .attr('fill', '#51cf66')
          .attr('font-weight', 'bold')
          .text(t.status.active)
      })
    }
  }

  function createParadoxTimeline(container) {
    if (!container) return

    d3.select(container).selectAll('*').remove()

    const width = 800
    const height = 300
    const svg = d3.select(container)
      .attr('width', width)
      .attr('height', height)

    const margin = { top: 30, right: 40, bottom: 30, left: 100 }
    const innerWidth = width - margin.left - margin.right
    const innerHeight = height - margin.top - margin.bottom

    const g = svg.append('g')
      .attr('transform', `translate(${margin.left},${margin.top})`)

    // Timeline data (2020-2024)
    const years = [2020, 2021, 2022, 2023, 2024]

    // Monitoring activity (high, consistent)
    const monitoringActivity = [
      { year: 2020, count: 25 },
      { year: 2021, count: 28 },
      { year: 2022, count: 30 },
      { year: 2023, count: 29 },
      { year: 2024, count: 27 }
    ]

    // Management action (low, sporadic)
    const managementAction = [
      { year: 2020, count: 3 },
      { year: 2021, count: 2 },
      { year: 2022, count: 1 },
      { year: 2023, count: 4 },
      { year: 2024, count: 2 }
    ]

    // Scales
    const xScale = d3.scaleLinear()
      .domain([2020, 2024])
      .range([0, innerWidth])

    const yScale = d3.scaleLinear()
      .domain([0, 35])
      .range([innerHeight, 0])

    // Axes
    g.append('g')
      .attr('transform', `translate(0,${innerHeight})`)
      .call(d3.axisBottom(xScale).tickFormat(d3.format('d')))

    g.append('g')
      .call(d3.axisLeft(yScale))

    // Monitoring activity area (top)
    const monitoringLine = d3.line()
      .x(d => xScale(d.year))
      .y(d => yScale(d.count))

    g.append('path')
      .datum(monitoringActivity)
      .attr('d', monitoringLine)
      .attr('fill', 'none')
      .attr('stroke', '#2989d8')
      .attr('stroke-width', 3)

    monitoringActivity.forEach(d => {
      g.append('circle')
        .attr('cx', xScale(d.year))
        .attr('cy', yScale(d.count))
        .attr('r', 4)
        .attr('fill', '#2989d8')
    })

    // Management action area (bottom)
    const actionLine = d3.line()
      .x(d => xScale(d.year))
      .y(d => yScale(d.count))

    g.append('path')
      .datum(managementAction)
      .attr('d', actionLine)
      .attr('fill', 'none')
      .attr('stroke', '#ff6b6b')
      .attr('stroke-width', 3)

    managementAction.forEach(d => {
      g.append('circle')
        .attr('cx', xScale(d.year))
        .attr('cy', yScale(d.count))
        .attr('r', 4)
        .attr('fill', '#ff6b6b')
    })

    // Gap area
    const gapArea = d3.area()
      .x((d, i) => xScale(years[i]))
      .y0((d, i) => yScale(managementAction[i].count))
      .y1((d, i) => yScale(monitoringActivity[i].count))

    g.append('path')
      .datum(years)
      .attr('d', gapArea)
      .attr('fill', '#ffcc00')
      .attr('fill-opacity', 0.3)

    // Gap label
    g.append('text')
      .attr('x', innerWidth / 2)
      .attr('y', innerHeight / 2)
      .attr('text-anchor', 'middle')
      .attr('font-size', '16px')
      .attr('font-weight', 'bold')
      .attr('fill', '#ffcc00')
      .text(t.timeline.gap)

    // Labels
    g.append('text')
      .attr('x', -10)
      .attr('y', yScale(28))
      .attr('text-anchor', 'end')
      .attr('font-size', '11px')
      .attr('fill', '#2989d8')
      .attr('font-weight', 'bold')
      .text(t.timeline.monitoring)

    g.append('text')
      .attr('x', -10)
      .attr('y', yScale(2))
      .attr('text-anchor', 'end')
      .attr('font-size', '11px')
      .attr('fill', '#ff6b6b')
      .attr('font-weight', 'bold')
      .text(t.timeline.action)
  }

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl font-bold text-lake-dark">{t.title}</h1>
        <p className="text-lg text-urgency-red font-semibold mt-2">{t.subtitle}</p>
        <p className="text-gray-700 mt-2 max-w-4xl">{t.description}</p>
      </div>

      {compareMode ? (
        <div className="grid grid-cols-2 gap-6">
          <div className="module-card current-state">
            <h3 className="text-xl font-bold text-urgency-red mb-2">{t.currentTitle}</h3>
            <p className="text-sm text-gray-600 mb-4">{t.currentDescription}</p>
            <svg ref={currentRef}></svg>
          </div>
          <div className="module-card ideal-state">
            <h3 className="text-xl font-bold text-hope-green mb-2">{t.idealTitle}</h3>
            <p className="text-sm text-gray-600 mb-4">{t.idealDescription}</p>
            <svg ref={idealRef}></svg>
          </div>
        </div>
      ) : (
        <div className="module-card current-state">
          <h3 className="text-xl font-bold text-urgency-red mb-2">{t.currentTitle}</h3>
          <p className="text-sm text-gray-600 mb-4">{t.currentDescription}</p>
          <svg ref={currentRef}></svg>
        </div>
      )}

      <div className="module-card bg-yellow-50 border-yellow-300">
        <h3 className="text-xl font-bold text-lake-dark mb-2">{t.timelineTitle}</h3>
        <p className="text-sm text-gray-600 mb-4">{t.timelineDescription}</p>
        <div className="flex justify-center">
          <svg ref={timelineRef}></svg>
        </div>
      </div>

      <div className="module-card bg-red-50 border-urgency-red">
        <h3 className="text-lg font-bold text-urgency-red mb-2">🔍 {t.keyFinding}</h3>
        <p className="text-gray-800">{t.keyFindingText}</p>
      </div>
    </div>
  )
}

export default InfoSystemsInfra
